.modal {
  --transition-time: .3s;
  z-index: 1000;
  cursor: pointer;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-time);
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.modal:before {
  content: "";
  vertical-align: middle;
  height: 100%;
  display: inline-block;
}

.modal__container {
  width: 1000px;
  vertical-align: middle;
  cursor: default;
  text-align: left;
  background-color: #fff;
  border-radius: 10px;
  margin: 50px auto;
  padding: 20px;
  display: none;
  position: relative;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 0;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: var(--transition-time);
}

.modal__container.modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transition: var(--transition-time);
  transform: translateY(-100px);
}

.fadeInUp.animate-open {
  opacity: 1;
  transition: var(--transition-time);
  transform: translateY(0);
}

[data-simplebar] {
  flex-flow: column wrap;
  align-content: flex-start;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
}

.simplebar-wrapper {
  width: inherit;
  height: inherit;
  max-width: inherit;
  max-height: inherit;
  overflow: hidden;
}

.simplebar-mask {
  direction: inherit;
  z-index: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  width: auto !important;
  height: auto !important;
}

.simplebar-offset {
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  direction: inherit !important;
  box-sizing: inherit !important;
  resize: none !important;
}

.simplebar-content-wrapper {
  direction: inherit;
  height: 100%;
  width: auto;
  max-width: 100%;
  max-height: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
  display: block;
  position: relative;
  box-sizing: border-box !important;
}

.simplebar-content-wrapper::-webkit-scrollbar, .simplebar-hide-scrollbar::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.simplebar-content:before, .simplebar-content:after {
  content: " ";
  display: table;
}

.simplebar-placeholder {
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  pointer-events: none;
}

.simplebar-height-auto-observer-wrapper {
  height: 100%;
  width: 100%;
  max-width: 1px;
  float: left;
  max-height: 1px;
  z-index: -1;
  pointer-events: none;
  flex-grow: inherit;
  flex-shrink: 0;
  flex-basis: 0;
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  box-sizing: inherit !important;
}

.simplebar-height-auto-observer {
  box-sizing: inherit;
  opacity: 0;
  height: 1000%;
  width: 1000%;
  min-height: 1px;
  min-width: 1px;
  pointer-events: none;
  z-index: -1;
  display: block;
  position: absolute;
  top: 0;
